<template>
  <div id="comparison-component">
    <div class="search-bar-container">
      <SearchBar></SearchBar>
      <div class="tabs-container">
        <FilterTag
          v-for="(tag, index) in searchValue"
          :key="index"
          :data="tag"
          @close="onTagCloseClick"
        ></FilterTag>
      </div>
    </div>

    <div class="chart-zoom-container">
      <el-row :gutter="30">
        <el-col :span="12">
          <BarChartCard title="Full Price Percentage"></BarChartCard>
        </el-col>
        <el-col :span="12">
          <BarChartCard title="Product Counts"></BarChartCard>
        </el-col>
        <el-col :span="12">
          <BarChartCard title="Price Segmentation"></BarChartCard>
        </el-col>
        <el-col :span="12">
          <BarChartCard title="Search Growth"></BarChartCard>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

import SearchBar from "@/components/SearchBar.vue";
import FilterTag from "@/components/FilterTag.vue";
import BarChartCard from "@/components/BarChartCard.vue";

const Component = defineComponent({
  name: "Comparison",
  components: {
    SearchBar,
    FilterTag,
    BarChartCard
  },
  setup() {
    const searchValue = ref(["Nike", "Puma", "The North Face"]);

    const onTagCloseClick = (tag: string) => {
      searchValue.value = searchValue.value.filter(_tag => _tag !== tag);
    };

    return {
      searchValue,

      onTagCloseClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#comparison-component
  page-container()

  .search-bar-container
    flex(flex-start, flex-start)
    margin-bottom 20px

    .tabs-container
      margin-left 15px

  .chart-zoom-container
    :deep() .el-col
      margin-bottom 30px
</style>
